<template>
	<div>
		<div class="header">
			<div class="backBtn"></div>
			<h1 class="headTitle"></h1>
		</div>
		<section class="pageCont putCash">
			<div class="bankCard">
				<div class="cardNo border">
					<h1 class="bankname" id="bankname">{{form.bankName}}</h1>
					<p><em>{{form.endNo}}</em></p>
				</div>
				<div class="price">
					<h1 class="title"></h1>
					<div class="number border">
						<i></i><input type="number" class="ipt" ref="ipt"/>
						<em class="all" @click="all"></em>
					</div>
					<p class="border">{{sum}}</p>
				</div>
				<ul class="submitMsg">
					<li class="border">
						
						<label>134 **** 1122</label>
					</li>
					<li  class="border">
						
						<label><input type="number" class="ipt"  placeholder=""/></label>
						<span class="send"></span>
					</li>
				</ul>
				<p class="notice">2</p>
			</div>
			<a href="javascript:" class="button mt30" id="submit" @click="submit"></a>
		</section>
		<layer :message="message" :hide.sync="hide"></layer>
	</div>
</template>
<script>
import layer from '../public/layer';
export default{
	data(){
		return {
			form:{
				cardNo:'313223007007',
				bankName:'',
				endNo:'',
				num:''
			},
			sum:'1',
			message:'',
			hide:true
		}
	},
	components:{layer},
	created:function(){
		const url = '../static/bankNormal.json';
		this.$axios.get(url).then((rs) => {
			for(let i=0;i<rs.data.length;i++){
				if(rs.data[i].code==this.form.cardNo){
					this.form.bankName=rs.data[i].name;
					let str = String(rs.data[i].code);
					this.form.endNo = str.substr(str.length-4,str.length);
				}
			}
		}).catch(function(error){
			console.log(error)
		})
	},
	methods:{
		all(){
			if(this.sum==0 || this.sum==0.00){
				this.message ="0";
				this.hide = false;
				return false;
			}
			if(this.$refs.ipt.value>this.sum){
				this.message ="";
				this.hide = false;
				return false;
			}
			if(this.$refs.ipt.value < 0){
				this.message ="";
				this.hide = false;
				return false;
			}
			this.$refs.ipt.value=this.sum;
		},
		submit(){
			if(this.$refs.ipt.value==0 || this.$refs.ipt.value==0.00){
				this.message ="";
				this.hide = false;
				return false;
			}
			if(this.$refs.ipt.value>this.sum){
				this.message ="";
				this.hide = false;
				return false;
			}
			this.form.num=this.$refs.ipt.value;
			this.$router.push({"path":"Success",query:this.form,abstract:true});
		}
	}
}
</script>



