<template>
	<div class="bgwhite">
		<div class="header">
			<div class="backBtn"></div>
			<h1 class="headTitle"></h1>
			<div class="main"><router-link to="/List"></router-link></div>
		</div>
		<section class="pageCont putCash">
			<div class="balance">
				<img src="@/assets/images/balance.png" />
				<h1 class="message"></h1>
				<p>{{price}}</p>
			</div>
			<ul class="add">
				<li><router-link to="/Reg">({{$store.state.ver}})</router-link></li>
			</ul>
			<a href="javascript:" class="button mt30" @click="get"></a>
		</section>
		<layer :message="message" :hide.sync="hide"></layer>
	</div>
</template>
<script>
import layer from '../public/layer'
import {mapMutations} from 'vuex'
export default {
  data(){
    return {
		price:1050.2,
		message:'...',
		hide:true,
		version:''
    }
  },
  components:{layer},
  created(){
		this.updateVersion("")
		
  },
  methods:{
    ...mapMutations([
		"updateVersion"
	]),
	get(){
		if(this.price == 0){
			this.message ="0";
			this.hide=false;
		}else{
			this.$router.push({"path":"Get"});
		}
	}
  }
}
</script>
<style lang="less">
	body{background:#fff;}
</style>




