<template>
	<div class="pagecont">
		<div class="pageLink"><a></a></div>
		<div class="tableform">
			<div class="formlist chooseform">
    			<div class="fgroup">
    				<i>ID/</i>
    				<input type="text" v-model="form.searchName" class="search" ref="searchContent" @keyup="search"/>
					<div class="searchList" :class={hide:ishide}>
						<ul>
							<li v-for="(item,i) in node" @click="select(item)">{{item.id}}/{{item.name}}</li>
						</ul>
					</div>
    			</div>
				<div class="fgroup">
    				<i></i>
    				<div class="select w100" data-cur="current">
    					<div class="txtSel" data-cur="current">
    						<a href="javascript:;" class="labTxt" data-cur="current">
    							<label class="jsBtLabel" data-cur="current" ></label>
    						</a>
    						<dl class="hide conList" data-cur="current"></dl>
    					</div>
    			    </div>
    			</div>
				<div class="fgroup">
    				<i></i>
    				<input type="text" class="search" tid="tid"  name="loginAccount">
    			</div>
    			<div class="fgroup">
    				<i></i>
					<input type="text" name="contactName">
    			</div>
    			<div class="fgroup">
    				<i></i>
					<input type="text" name="contactPhone">
    			</div>
    			<div class="fgroup">
    				<i></i>
					<div class="select w100" data-cur="current">
    					<div class="txtSel" data-cur="current">
    						<a href="javascript:;" class="labTxt" data-cur="current">
    							<label class="jsBtLabel" data-cur="current" id="selectSalesman"></label>
    						</a>
    						<dl class="hide conList" data-cur="current"></dl>
    					</div>
    			    </div>
    			</div>
    			<div class="fgroup">
    				<i></i>
    				<div id="locality" data-cur="current">
    					<div id="ssqSelector11">
							<div class="select w100" data-cur="current">
								<div class="txtSel" data-cur="current">
									<a href="javascript:;" class="labTxt" data-cur="current">
									<label data-cur="current" class="jsBtLabel" title=""></label></a>
									<dl class="hide conList ssq_province" data-cur="current"></dl>
								</div>
						    </div>
							<div class="select w100" data-cur="current">
								<div class="txtSel" data-cur="current">
									<a href="javascript:;" class="labTxt" data-cur="current">
									<label class="jsBtLabel" data-cur="current" title="/">/</label></a>
									<dl class="hide conList ssq_country" data-cur="current"></dl>
								</div>
						    </div>
						</div>
    				</div>
    			</div>
				<div class="fgroup">
    				<i></i>
    				<div class="select w100" data-cur="current">
    					<div class="txtSel" data-cur="current">
    						<a href="javascript:;" class="labTxt" data-cur="current">
    							<label class="jsBtLabel" data-cur="current" id="selectMerchantStatus" title="" data-state="0" style="color: rgb(0, 0, 0);"></label>
    						</a>
    						<dl class="hide conList" data-cur="current"></dl>
    					</div>
    			    </div>
    			</div>
				<div class="fgroup singtime">
					<i class="name"></i>
					<div class="timebox">
						<input id="startTime" type="text" readonly="readonly" class="singledate" name="startTime">
						<i class="ico-time"></i>
					</div>
				</div>	
    			<div class="formlistbar">
    				<a class="btn btn-b"></a>
    				<a class="btn"></a>
    			</div>
    		</div>
		</div>
	</div>
</template>
<script>
	import api from '../api/Adindex.js'
	export default{
		data(){
			return{
				ishide:true,
				node:'',
				form:{
					searchName:''
				}
			}
		},
		methods:{
			search(){
				this.ishide = false;
				let mvalue = this.$refs.searchContent.value;
				api.addForm({mvalue:mvalue}).then(rs=>{
					this.node = rs;
				})
			},
			select(item){
				this.form.searchName =`${item.id}/${item.name}`;
				this.ishide=true;
			}
		}
	}
</script>


