<template>
	<div class="regList">
		<ul>
			<li>
				<label></label>
				<input type="text" class="ipttxt" ref="ipt"  v-model="form.username" placeholder=""/>
			</li>
			<li>
				<label></label>
				<input type="password"  class="ipttxt" v-model="form.pwd" placeholder=""/>
			</li>
			<li>
				<label></label>
				<input type="text" class="ipttxt" v-model="form.spwd" placeholder=""/>
			</li>
			<li>
				<label></label>
				<input type="text" class="ipttxt" placeholder=""/>
			</li>
			<li>
				<label></label>
				<input type="text" class="ipttxt" v-model="form.number" placeholder=""/>
			</li>
			<li>
				<label></label>
				<input type="text" class="ipttxt" v-model="form.email" placeholder=""/>
			</li>
			<li>
				<label></label>
				<span @click="selAddress">{{address}}</span>
			</li>
			<li>
				<label></label>
				<span>{{form.version}}</span>
			</li>
		</ul>
		<div>{{message}}</div>
		<div class="error" :class="{hide:hide}">{{errorMsg}}</div>
		<a href="javascript:" class="button mt30" @click="addVip"></a>
		<selCity :hide.sync="hide" @area="area"></selCity>
	</div>
</template>
<script>
	import layer from '../public/layer.vue'
	import selCity from '../public/select.vue'
	import {Validate} from  '../assets/js/validate.js'
	//import {Child} from '../assets/ts/geeter.js'
	let lodash = require('lodash');
	let vm;
	import {mapState,mapMutations} from 'vuex'
	export default{
		data(){
			return{
				validate:new Validate(),
				pwd:'',
				hide:false,
				errorMsg:'',
				address:'',
				hide:true,
				form:{
					username:'',
					pwd:'',
					spwd:'',
					number:'',
					email:'',
					version:''
				},
				message:''
			}
		},
		components:{layer,selCity},
		created(){
			vm = this;
			this.updateVersion("");
			this.form.version = this.$store.state.ver;
			//var parent = new Parent("");
			//var child=new Child(this.form.username);
			//this.message = child.getinfo()
		},
		watch:{
			'form.username':'name',
			'form.pwd':'password',
			'form.spwd':'checkpwd',
			'form.number':'telphone',
			'form.email':'emailEvent',
			deep:true
		},
		computed:{
			count(){
				return this.$store.state.ver 
			}
		},
		methods:{
			...mapMutations([
				"updateVersion",
				"cremt"
			]),
			addVip:function(){
				//this.$axios.get(url,{params:this.form}).then((rs)=>{
					//console.log(rs)
				//})
				this.updateVersion(this.$refs.ipt.value);
				console.log(this.$store.state.ver)
				this.cremt(this.$refs.ipt.value);
				console.log(this.$store.state.count)
			},
			area(val){
				this.address=val;
				this.hide=true;
			},
			name:lodash.debounce(function(){
				vm.errorMsg = this.validate.sw("username",this.form.username);
			},1000),
			password:lodash.debounce(function(){
				vm.errorMsg = this.validate.sw("pwd",this.form.pwd);
			},1000),
			checkpwd:lodash.debounce(function(){
				if(this.form.spwd!=this.form.pwd){
					vm.errorMsg=this.validate.sw("spwd",this.form.spwd);
				}
			},1000),
			telphone:lodash.debounce(function(){
				vm.errorMsg=this.validate.sw("number",this.form.number);
			},1000),
			emailEvent:lodash.debounce(function(){
				vm.errorMsg=this.validate.sw("email",this.form.email);
			},1000),
			selAddress(){
				this.hide=false;
			}
		}
	}
</script>



