<template>
	<div class="success">
		<div class="header">
			<h1 class="headTitle"></h1>
		</div>
		<section class="pageCont putCash">
			<div class="border cashSate">
				<img src="../assets/images/right.png" />
				<h1 class="message right"></h1>
				<p>2</p>
			</div>
			<ul class="cardInfo">
				<li class="border"><em>{{num}}</em></li>
				<li class="border"><em>{{this.cardNo}}</em></li>
				<li class="border"><em>{{time}}</em></li>
				<li class="border"><em>{{this.endNo}}</em></li>
			</ul>
			<router-link to="/List"  class="button mt30"></router-link>
		</section>
	</div>
</template>
<script>
export default{
	data(){
		return{
			cardNo:this.$route.query.cardNo,
			num:this.$route.query.num,
			endNo:this.$route.query.endNo,
			time:''
		}
	},
	created:function(){
		this.time = this.$store.state.date(new Date());
		this.$store.commit("cremt");
	}
}
</script>


